@import url(https://fonts.googleapis.com/css2?family=Chivo:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Chivo', sans-serif;
  margin: 0;  
  padding: 0;
  border: 0;
  text-decoration: none;
}

.spinner {
  animation: spin infinite 5s linear;
}

@keyframes spin {
from {
  transform: rotate(0deg);
}
to {
  transform: rotate(360deg);
}
}

.navBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #1f1f1f;
    padding: 16px 10%;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    z-index: 10;
}

.NavLink {
    color: white;
    transition: 300ms;
}

.NavLink:hover {
    color: #fddc5b;
    transform: translateY(-4px);
}

.siteLogo {
    font-size: 36px;
    margin-bottom: 8px;
}

.siteTitle {
    font-size: 20px;    
}

.logoContainer {
    color: #fddc5b;
    display: flex;
    align-items: center;
    transition: 300ms;
}

.logoContainer:hover {
    transform: scale(1.1);
}

.mainMenu {
    display: flex;
    grid-gap: 32px;
    gap: 32px
}
.cartWidget {
    display: flex;
    align-items: center;
    color: #fddc5b;
    transition: 300ms;
}

.cartWidget:hover {
    transform: scale(1.1);
}

.cartWidget .cartCounter {
    display: inline;
    margin-left: 8px;
}

.cartCounterNone {
    display: none;
}
.ItemDetailContainer {
    min-height: 50vh;
    display: flex;
    justify-content: center;
    margin: 0 10%;
    padding: 5vh 0;
}

.imgItemDetailContainer {
    margin: 32px;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 60vh;
    overflow: hidden;
}

.imgItemDetail {
    width: 90%;
    max-width: 512px;
}

.ItemDetail {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 24px;
    gap: 24px;
}
.itemCountContainer {
    width: 160px;
}

.ItemCount {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    border: solid 1px rgb(190, 190, 190);
    border-radius: 4px;
    padding: 4px;
}

.counter {
    display: inline;
    font-size: 20px;
    margin: 0;
}

.counterButton {
    background-color: white;
}

.addToCart {
    background-color: #FDDC5B;
    border: none;
    font-size: 16px;
    color: white;
    padding: 8px;
    margin-top: 4px;
    border-radius: 16px;
    transition-duration: 300ms;
    width: 160px;
}

.addToCart:hover {
    transform: scale(1.05);
}

.disabled {
    background-color: rgb(206, 206, 206);
    color: rgb(238, 238, 238);
}
.button {
    border: none;
    font-size: 16px;
    padding: 8px;
    margin-top: 4px;
    border-radius: 16px;
    transition-duration: 300ms;
    width: 160px;
}

.btnActive {
    background-color: #FDDC5B;
    color: white;
}

.btnActive:hover {
    transform: scale(1.05);
}

.btnDisabled {
    background-color: rgb(206, 206, 206);
    color: rgb(238, 238, 238);
}
.spinnerContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
}
.errorContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 50vh;
    grid-gap: 16px;
    gap: 16px;
    padding: 5vh 0;
}

.RelatedProducts {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.item {
    width: 240px;
    min-height: 260px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    grid-gap: 16px;
    gap: 16px;
    padding: 16px;
    border-radius: 8px;
    box-shadow: 2px 2px 10px 4px rgba(0, 0, 0, 0.200);
    transition-duration: 300ms;
}

.item:hover {
    transform: scale(1.02);
}

.itemImageContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 150px;
    height: 150px;
}

.itemImage {
    max-height: 150px;
    max-width: 150px;
}

.itemTitle {
    color: #3a3a3a;
    font-size: 16px;
    font-weight: 500;
}

.itemPrice {
    color: #3a3a3a;
    font-size: 18px;
    font-weight: 600;
}
.ItemList {
    max-width: 1200px;
    margin: 10vh 10vw;
    display: flex;
    justify-content: center;
    align-items: stretch;
    text-align: center;
    flex-wrap: wrap;
    grid-gap: 20px;
    gap: 20px;
}
.cartContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 50vh;
    grid-gap: 16px;
    gap: 16px;
    padding: 5vh 0;
}

.cartItem {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 800px;
    border-bottom: solid 1px #bbbbbb;
    padding: 16px;
}

.cartItemImgContainer {
    width: 20%;
}

.cartItemImg {
    width: 100px;
    max-width: 100px;
    align-self: center;
}

.cartItemDetail {
    width: 50%;
    display: inline-block;
    padding: 16px;
}

.cartItemPrice {
    width: 20%;
}

.cartTotalPrice {
    padding: 16px;
    font-size: 20px;
}

.removeItem {
    width: 10%;
    color: #d64f4f;
}

.cartButtons {
    display: flex;
    grid-gap: 16px;
    gap: 16px;
}
.checkoutContainer {
    display: flex;
    flex-direction: column;
    min-height: 50vh;
    grid-gap: 16px;
    gap: 16px;
    padding: 5vh 0;
    text-align: center;

}

.checkoutColumns {
    display: flex;
    align-items: center;
}
.formContainer {
    width: 50%;
}

.formContainer form {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    grid-gap: 8px;
    gap: 8px;
    padding: 32px;

}

.formContainer form input{
    width: 80%;
    max-width: 400px;
    flex-direction: column;
    border: 1px solid #9b9b9b;
    border-radius: 16px;
    font-size: 16px;
    padding: 8px;
}

.successfulOrder {
    width: 50%;
}
.checkoutDetail {
    width: 50%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 16px;
    gap: 16px;
}

.checkoutItem {
    display: flex;
    align-items: center;
    grid-gap: 16px;
    gap: 16px;
    padding-bottom: 16px;
    border-bottom: solid 1px grey;
}

.checkoutItemImgContainer {
    display: flex;
    align-items: center;
    width: 80px;
    height: 80px;
}

.checkoutItemImg {
    width: 80px;
}

.checkoutItemDetail {
    text-align: left;
}

.checkoutTotalPrice {
    font-size: 20px;
    text-align: center;
}
.Footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #1f1f1f;
    padding: 5vh 10%;
    border-bottom: 1px solid grey;
}

.footerNav {
    display: flex;
    flex-direction: column;
}

.siteMap {
    font-size: 18px;
    color: #fddc5b;
}

.footerNavLink {
    color: white;
    transition: 300ms;
    padding: 2px 0;
}

.footerNavLink:hover {
    color: #fddc5b;
    transform: translateX(8px);
}

.credits {
    background-color: #1f1f1f;
    text-align: center;
    padding: 8px 0;
}

.credits a {
    color: #fddc5b;
}
.ItemListContainer {
    display: flex;
    justify-content: center;
}
